    str = str.replace(/&lsaquo;/g, '‚Äπ');
    str = str.replace(/&rsaquo;/g, '‚Ä∫');
    str = str.replace(/&oline;/g, '‚Äæ');
    str = str.replace(/&euro;/g, '‚Ç¨');
